.advantages {
  position: relative;
  padding-top: 112px;

  &::before {
    content: "";
    position: absolute;

    left: 10.2%;
    top: 45px;

    width: 300px;
    height: 300px;

    background-image: url("../img/triangle-advantages.png");
    background-repeat: no-repeat;
  }

  &::after {
    content: "";
    position: absolute;

    right: 1.1%;
    top: 420px;

    width: 300px;
    height: 300px;

    background-image: url("../img/triangle-advantages.png");
    background-repeat: no-repeat;

    transform: rotate(180deg);

    z-index: -1;
  }

  @media (max-width: $tablet-width) {
    padding: 0;
    padding-top: 55px;

    &::before {
      content: none;
    }

    &::after {
      content: none;
    }
  }

  @media (max-width: $mobile-width) {
    padding-top: 37px;
  }
}

.advantages__wrapper {
  position: relative;
  padding-left: 3.6%;
  max-width: 1560px;

  @media (max-width: $tablet-width) {
    padding-left: 24px;
    padding-right: 24px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 14px;
    padding-right: 16px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  padding-left: 8.5%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;

  list-style: none;

  @media (max-width: $tablet-width) {
    padding-left: 0;
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
  }
}


.advantages__list h3 {
  margin: 0;
  margin-bottom: 16px;
  padding: 0;

  font-family: "RobotoCondesed", "Arial", sans-serif;
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;

  color: $blue;

  @media (max-width: $tablet-width) {
    margin-bottom: 7px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 8px;

    font-size: 20px;
    line-height: 140.62%;
  }
}

.advantages__list li {
  position: relative;
  margin-right: 30px;
  margin-bottom: 30px;
  padding-top: 94px;
  padding-right: 42px;
  padding-bottom: 65px;
  padding-left: 41px;

  width: 41.7%;
  min-height: 299px;

  background-color: #ffffff;

  border: 1px solid #ebebeb;
  border-left: 3px solid #00b2ff;

  &:hover h3,
  &:hover svg,
  &:hover {
    background-color: #00b2ff;
    fill: #ffffff;
    color: $white;

    cursor: pointer;
  }


  @media (max-width: $tablet-width) {
    padding-top: 66px;
    padding-right: 20px;
    padding-bottom: 20px;
    padding-left: 26px;

    width: 47.9%;

    min-height: 233px;

    border-left: 2px solid #00b2ff;
  }

  @media (max-width: $mobile-width) {
    margin-right: 0;
    margin-bottom: 20px;

    padding-top: 71px;
    padding-right: 18px;
    padding-bottom: 25px;
    padding-left: 24px;

    width: 100%;

    &.advantages__list-hired {
      padding-right: 10px;
    }

    &.advantages__list-detective {
      padding-top: 65px;

      & h3 {
        margin-bottom: -8px;
      }
    }
  }
}

.advantages__list li:nth-of-type(2n) {
  margin-right: 0;
}

.advantages__list svg {
  position: absolute;
  fill: #00b2ff;

  top: 40px;
  left: 42px;

  @media (max-width: $tablet-width) {

    top: 23px;
    left: 25px;

    width: 30px;
    height: 36px;
  }
}

.advantages__list p {
  margin: 0;
  padding: 0;

  font-size: 16px;
  line-height: 22px;
}
